@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,600;0,700;1,500&family=Open+Sans:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@100;400;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  vertical-align: baseline;
}
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --primary-element-color: #eb5440;
  --secondary-element-color: #4a4b4c;
  --tertiary-element-color: #e8e8e8;
  --tertiary-element-gray-color: #c3c3c3;

  --background-1-color: #442357;
  --background-2-color: #ffffff;

  --primary-text-color: #000000;
  --secondary-text-color: #4a4b4c;
  --text-color: #ffffff;

  --hyperlink-text-color: #067bc2;

  --border-radius: 8px;
  --main-font: 'Montserrat', sans-serif;
  --secondary-font: 'Open Sans', sans-serif;
  --menu-font: 'Rubik Mono One', sans-serif;
  --transition: 0.5s ease all;

  --max-width: 1370px;
}

body {
  font-family: -apple-system, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
  scroll-behavior: smooth;
  margin: 0 auto;
  max-width: 1100px;
}
button {
  background: unset;
  border: none;
  cursor: pointer;
}
a {
  color: inherit;
  text-decoration: none;
}
.content-wrapper {
  min-height: 100vh;
  width: 100%;
  padding: 0px 170px;
  position: relative;
}
.context {
  text-align: center;
}
.middle-wrapper {
  background-color: #282c34;
  min-height: calc(100vh - 110px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  color: var(--text-color);
}

/* Main background style */
.bg-style-wrapper {
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
  body {
    color: #e6e6e6;
    background: #111;
  }
}

@media (max-width: 1250px) {
  .content-wrapper {
    padding: 0px 100px;
  }
}
@media (max-width: 648px) {
  .content-wrapper {
    min-height: unset;
    padding: 0px 30px;
  }
}

.home-wrapper {
  background-color: antiquewhite;
  margin: 20px 0;
  width: 100%;
  grid-gap: 30px;
  gap: 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

/*  Navbar */

.navbar {
  font-family: var(--main-font);
  z-index: 100;
  height: 110px;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding-top: 23px;
}
.navbar::after {
  content: '';
  position: absolute;
  top: 70px;
  left: 0px;
  width: 100%;
  border-bottom: 1px solid transparent;
  pointer-events: none;
  transition: border 0.3s ease 0s;
}
.desktop-menu {
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.mobile-menu {
  display: none;
  height: 100%;
  position: relative;
}
.drop-menu {
  height: 100vh;
  width: 100vw;
  pointer-events: none;
  background-color: rgb(17, 17, 17);
  display: flex;
  flex-direction: column;
  padding: 130px 30px 41px;
  position: fixed;
  top: 0px;
  left: 0px;
  -webkit-transform: translateX(calc(100% + 100px));
          transform: translateX(calc(100% + 100px));
  transition: -webkit-transform 0.5s ease 0s;
  transition: transform 0.5s ease 0s;
  transition: transform 0.5s ease 0s, -webkit-transform 0.5s ease 0s;
}
.hamburger-bn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
/* use in js */
/* Opening menu */
.menu-open {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  pointer-events: initial;
  transition: -webkit-transform 0.6s ease 0s;
  transition: transform 0.6s ease 0s;
  transition: transform 0.6s ease 0s, -webkit-transform 0.6s ease 0s;
}
.mob-animation {
  font-family: var(--menu-font);
  font-size: 40px;
  min-height: 80px;
  text-transform: uppercase;
  position: relative;
  mix-blend-mode: difference;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0.1;
  -webkit-transform: translateX(-100px);
          transform: translateX(-100px);
  transition: opacity 0.9s ease 0.1s, -webkit-transform 0.8s ease 0.1s;
  transition: opacity 0.9s ease 0.1s, transform 0.8s ease 0.1s;
  transition: opacity 0.9s ease 0.1s, transform 0.8s ease 0.1s, -webkit-transform 0.8s ease 0.1s;
}
.menu-open .mob-animation {
  opacity: 1;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.wrapper-links {
  width: 300px;
  display: flex;
  justify-content: space-evenly;
}
.nav-link {
  padding: 5px;
}
.nav-link > a {
  color: var(--text-color);
  background: var(--background-1-color);
}
.nav-projects {
}
.nav-link:hover,
.nav-link:focus-within {
  cursor: pointer;
  /* background: rgba(255, 0, 0, 0.486); */
}
.dropdown {
  background: orange;
  /* visibility: hidden; */
  opacity: 0;
  /* min-width: 5rem; */
  /* position: absolute; */
  transition: all 0.5s ease;
  /* margin-top: 59px; */
  /* top: 59px; */
  /* left: 115px; */
  display: none;
}
.nav-link:hover > .dropdown,
.nav-link:focus-within > .dropdown,
.dropdown:hover {
  visibility: visible;
  opacity: 1;
  /* display: block; */
}
ul li ul li {
  clear: both;
  width: 100%;
}

@media (max-width: 648px) {
  .navbar {
    padding-top: 0px;
  }
  .desktop-menu {
    display: none;
  }
  .mobile-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.projects-wrapper {
  background-color: antiquewhite;
  margin: 20px 0;
  width: 100%;
  grid-gap: 30px;
  gap: 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  background-color: blueviolet;
  border: 10px solid rgb(47, 40, 56);
}

:root {
  /* ====== Calculator ====== */

  /* --background-calculator-color: rgb(243, 236, 236); */

  --background-light-mode: #fff4ac;
  --main-light-mode: #f6da73;
  --buttons-light-mode: #ffe482;
  --display-light-mode: #feffe1;
  --shadow-light-mode: #6c6c6c;
  --text-light-mode: #292929;

  --background-dark-mode: #2d2e34;
  --main-dark-mode: #252627;
  --buttons-dark-mode: #6d6d6d;
  --display-dark-mode: #3c3d3e;
  --shadow-dark-mode: #101010b3;
  --text-dark-mode: #ffffff;

  --border-radius-main: 1.2rem;
  --formula-color: rgb(142, 142, 142);
}
#dark {
  background-color: #2d2e34;
  background-color: var(--background-dark-mode);
}
#light {
  background-color: #fff4ac;
  background-color: var(--background-light-mode);
}
.wrapper-calculator {
  display: flex;
  min-height: 100vh;
  min-width: 100%;
  margin: auto;
  font-family: 'Orbitron', sans-serif;
  align-items: center;
  justify-content: center;
}
.calculator {
  width: 329px;
  padding: 0.9rem;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.35);
  border-radius: 1.2rem;
  border-radius: var(--border-radius-main);
  font-family: 'Orbitron', sans-serif;
}
#light .calculator {
  background-color: #f6da73;
  background-color: var(--main-light-mode);
}
#dark .calculator {
  background-color: #252627;
  background-color: var(--main-dark-mode);
  border: 1px solid #101010b3;
  border: 1px solid var(--shadow-dark-mode);
}
.top-section {
  display: grid;
  grid-gap: 5%;
  gap: 5%;
  grid-template-columns: 15% 60% 15%;
  align-items: center;
  justify-items: center;
  height: 5rem;
}
.top-section h3 {
  font-weight: 600;
}
/* unite  with the same */
#dark .top-section {
  color: #ffffff;
  color: var(--text-dark-mode);
}
#light .top-section {
  color: #292929;
  color: var(--text-light-mode);
}

/* ===== DISPLAY ===== */

.display {
  display: grid;
  align-items: center;
  height: 5.8rem;
  padding: 0.5rem 1rem;
  text-align: right;
  color: var(--light-calculator-color);
  overflow: hidden;
  border-radius: 0.8rem;
}

#dark .display {
  background-color: #3c3d3e;
  background-color: var(--display-dark-mode);
}
#light .display {
  background-color: #feffe1;
  background-color: var(--display-light-mode);
}
.formula {
  color: rgb(142, 142, 142);
  color: var(--formula-color);
  font-size: 0.8rem;
  font-weight: 400;
  overflow: hidden;
  height: 1.2rem;
}
.results {
  font-size: 2rem;
  font-weight: 400;
  overflow: hidden;
}
/* unite  with the same */
#dark .results {
  color: #ffffff;
  color: var(--text-dark-mode);
}
#light .results {
  color: #292929;
  color: var(--text-light-mode);
}

/* ===== BUTTON PAD ===== */
.buttons-pad-calculator {
  padding: 2rem 0;
}
.button-grid {
  display: grid;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  align-items: center;
}
.grid-row-span-2 {
  grid-row: span 2;
  height: 7.5rem !important;
}

.buttons-pad-calculator button {
  height: 3.5rem;
  width: 3.5rem;
  font-size: 1.2rem;
  border-radius: 4rem;
  cursor: pointer;
  font-family: 'Orbitron', sans-serif;
}

#light .buttons-pad-calculator button {
  background-color: #ffe482;
  background-color: var(--buttons-light-mode);
  border: 3px solid #f5d86d;
  box-shadow: 3px 4px 4px rgb(0 0 0 / 40%);
}
#light .buttons-pad-calculator button:hover {
  background-color: #f7e08e;
  transition: 0.2s;
}
#light .buttons-pad-calculator button:active {
  box-shadow: none;
}

#dark .buttons-pad-calculator button {
  background-color: #6d6d6d;
  background-color: var(--buttons-dark-mode);
  border: 3px solid #837f7f;
  box-shadow: 3px 1px 2px 2px #101010b3;
  box-shadow: 3px 1px 2px 2px var(--shadow-dark-mode);
}
#dark .buttons-pad-calculator button:hover {
  background-color: #767676;
  transition: 0.2s;
}
#dark .buttons-pad-calculator button:active {
  box-shadow: none;
}

:root {
  /* ==== Drum Pad ==== */

  --main-bg-color-drumPad: rgb(49, 51, 82);
  --inside-border-drumPad: 2px solid #ff69b4;
  --main-color-object-drumPad: #aaa;
  --hotpink: #ff69b4;
  --border-slider-range-drumPad: 2px solid var(--hotpink);
  --yellow: #ffe94e;
  --hotpurple: #d72df7;
  --purple: #5016f1;
  --amazing-colors: linear-gradient(
    217deg,
    var(--yellow),
    var(--hotpurple),
    var(--purple)
  );
}

.wrapper-drum-machine {
  /* margin: 9rem auto; */
  display: grid;
  grid-gap: 0.8em;
  gap: 0.8em;
  grid-template-columns: 480px 200px;
  width: 700px;
  height: 400px;
  align-content: stretch;
  /* justify-content: space-evenly; */
}
.style-wrapper {
  font-family: 'Lato', sans-serif;
  border: 3px solid #333;
  background-color: rgb(49, 51, 82);
  background-color: var(--main-bg-color-drumPad);
  color: #aaa;
  color: var(--main-color-object-drumPad);
}

#drumPad,
.controls-block {
  border: 2px solid hotpink;
  width: 100%;
  height: 100%;
}
#drumPad {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1em;
  gap: 1em;
  padding: 1em;
}
.drum-pad {
  text-align: center;
  border: 2px solid rgb(190, 190, 190);
  justify-content: center;
  display: table-row-group;
  font-size: 28px;
}
.letterOnPad {
  display: block;
  margin-top: 2rem;
  cursor: default;
}

/* ======== Style for pressed button ======== */

@-webkit-keyframes shine {
  0% {
    background-color: #5016f1;
    background-color: var(--purple);
  }
  80% {
    background-color: #d72df7;
    background-color: var(--hotpurple);
  }
  100% {
    background-color: #ffe94e;
    background-color: var(--yellow);
  }
}

@keyframes shine {
  0% {
    background-color: #5016f1;
    background-color: var(--purple);
  }
  80% {
    background-color: #d72df7;
    background-color: var(--hotpurple);
  }
  100% {
    background-color: #ffe94e;
    background-color: var(--yellow);
  }
}
.drum_button_active {
  -webkit-animation-name: shine;
          animation-name: shine;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
  color: transparent;
}

/* ====== Style for control block ======= */
.controls-block {
  display: grid;
  justify-content: space-evenly;
  justify-items: center;
  align-items: center;
  padding: 20px 10px;
  font-size: 1.2rem;
}
#display-drum {
  border: 2px solid #ff69b4;
  border: var(--inside-border-drumPad);
  margin: 10px auto;
  text-align: center;
  width: 160px;
  height: 37px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block {
  display: block;
  width: 100%;
}
.block p {
  margin-bottom: 7px;
  display: inline-block;
}

/* ====== Style for radio button ====== */

.custom-radio-btn {
  width: 31px;
  height: 31px;
  border: 2px solid #ff69b4;
  border: 2px solid var(--hotpink);
  border-radius: 50%;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 7px;
}
.custom-radio-btn .checkMark {
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  border-radius: 50%;
  display: inline-block;
  background-color: #ff69b4;
  background-color: var(--hotpink);
  opacity: 0;
  transition: opacity 0.35s;
}
.custom-radio-btn input {
  display: none;
}
.custom-radio-btn input:checked + .checkMark {
  opacity: 1;
}
.label-button {
  display: flex;
  color: #aaa;
  color: var(--main-color-object-drumPad);
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: baseline;
}
.label-text {
  margin-left: 15px;
}

/* ================== Style input range and sound volume ===============*/
.display-volume {
  display: flex;
  margin: 0.4rem 1rem;
  align-items: center;
}
#displayVolume {
  border: 2px solid #ff69b4;
  border: var(--inside-border-drumPad);
  display: inline-block;
  margin: 0 1rem;
  font-size: 1.2rem;
  text-align: center;
  width: 2.4rem;
  height: 2.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.style-range {
  /* -webkit-appearance: none; */
  width: 100%;
  height: 14px;
  background: linear-gradient(
    217deg,
    #ffe94e,
    #d72df7,
    #5016f1);
  background: var(--amazing-colors);
  outline: none;
  border: 2px solid #ff69b4;
  border: var(--border-slider-range-drumPad);
  border-radius: 8px;
  margin-top: 10px;
}
.style-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 30px;
  background: rgb(49, 51, 82);
  background: var(--main-bg-color-drumPad);
  cursor: default;
  border: 2px solid #ff69b4;
  border: var(--border-slider-range-drumPad);
  border-radius: 4px;
}
.style-range::-moz-range-thumb {
  width: 14px;
  height: 30px;
  background: rgb(49, 51, 82);
  background: var(--main-bg-color-drumPad);
  cursor: default;
  border: 2px solid #ff69b4;
  border: var(--border-slider-range-drumPad);
  border-radius: 4px;
}

