:root {
  /* ==== Timer Countdown ==== */
  --outside-border: 2px solid var(--color-secondary-dark);
  --inside-border: 2px solid var(--color-primary-dark);
  --display-border: 6px solid var(--color-secondary-light);

  --timer-bg-color: #012442;
  --font-color: #aaa;
  --color-primary-dark: #e38664;
  --color-primary-light: #ffa566;
  --color-secondary-light: #7e627b;
  --color-secondary-dark: #5ba0bf;
}

.wrapper-timer {
  display: flex;
  min-height: 100vh;
  width: 100%;
  font-family: 'Orbitron', sans-serif;
  align-items: center;
  justify-content: center;
}
.wrapper-timer-style {
  background: #012442;
  background: var(--timer-bg-color);
  color: #aaa;
  color: var(--font-color);
  font-size: 2.5rem;
}
.timer-control-panel {
  display: grid;
  text-align: center;
  align-items: stretch;
}
.timer-control-panel h2 {
  background: -webkit-linear-gradient(
    90deg,
    var(--color-primary-light),
    var(--color-secondary-light),
    var(--color-secondary-dark)
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* =========Control panel ========= */

.control-panel {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 6rem;
  gap: 6rem;
  text-align: center;
  align-items: stretch;
}
.control-bar {
  width: 20rem;
  padding: 1rem;
  font-size: 32px;
  color: #aaa;
  color: var(--font-color);
}

.grid-control-bar {
  display: grid;
  grid-template-columns: 20% 60% 20%;
  align-items: center;
  justify-items: center;
  padding: 1rem;
  margin-top: 1rem;
}
.grid-control-bar-style {
  font-size: 40px;
}
/* ======= Buttons ======== */
.button-with-icon {
  width: 6rem;
  background: #012442;
  background: var(--timer-bg-color);
  cursor: pointer;
}
.timer-icon-style,
.timer-pause-icon,
.timer-play-icon {
  width: 2.5rem;
  height: 2.5rem;
}
.timer-icon-style {
  color: #aaa;
  color: var(--font-color);
}
.timer-icon-style:hover {
  color: #ffa566;
  color: var(--color-primary-light);
}

.timer-pause-icon {
  color: #ffa566;
  color: var(--color-primary-light);
}

.timer-play-icon {
  color: #e38664;
  color: var(--color-primary-dark);
}
/* ======= Display Timer ======== */

#timer-label,
#time-left {
  margin: 1rem auto;
}
#timer-label {
  background: -webkit-linear-gradient(
    90deg,
    var(--color-primary-light),
    var(--color-secondary-light)
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

#time-left {
  border: 6px solid #7e627b;
  border: var(--display-border);
  padding: 1.5rem;
  width: 15rem;
  color: white;
}

/* ======= Timer Control Bar ======== */
.timer-control button {
  background-color: #012442;
  background-color: var(--timer-bg-color);
  margin: 1rem 1rem;
}

/* ======= Animation for refresh icon ======== */
@-webkit-keyframes rotateIcon {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
@keyframes rotateIcon {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}
#reset-animation:hover {
  -webkit-animation-name: rotateIcon;
          animation-name: rotateIcon;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

/* ============= MEDIA ============= */

@media screen and (max-width: 740px) {
  .control-panel {
    grid-template-columns: none;
    grid-gap: 1rem;
    gap: 1rem;
    justify-items: center;
  }
}

:root {
  /* ==== Quote Block ==== */
  --border-radius-qb: 0.5rem;
  --margin-context-qb: 1.5rem auto;
  --font-size-qb: 1.5em;
}
.pageQuote {
  display: grid;
  min-height: 100vh;
  width: 100%;
  justify-content: center;
  align-content: center;
  align-items: center;
  font-family: 'Barlow', sans-serif;
}

#title,
.footer {
  font-size: 2rem;
  position: relative;
  color: white;
  text-align: center;
  margin: 0 auto 2rem auto;
}

#quote-box {
  display: grid;
  max-width: 35rem;
  min-width: 22rem;
  min-height: 18rem;
  padding: 1.5em;
  border-radius: 0.5rem;
  border-radius: var(--border-radius-qb);
  background-color: white;
  text-align: center;
}

.context {
  text-align: center;
  position: relative;
}

#text {
  text-align: center;
  font-size: 1.5em;
  font-size: var(--font-size-qb);
  margin-top: 2rem;
}

#author {
  position: relative;
  margin: 1.5rem auto;
  margin: var(--margin-context-qb);
  font-size: 1.5em;
  font-size: var(--font-size-qb);
  text-align: right;
  font-weight: 600;
}

.buttons-quote {
  width: 100%;
  margin: auto;
  display: inline-block;
}

.buttons-quote #tweet-quote,
.buttons-quote #new-quote {
  font-size: 20px;
  height: 40px;
  color: white;
  border: 0;
  border-radius: 0.5rem;
  border-radius: var(--border-radius-qb);
  cursor: pointer;
}

#twit-icon {
  margin-right: 15px;
}

#tweet-quote {
  float: left;
  width: 210px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
#new-quote {
  float: right;
  width: 115px;
}
#new-quote:hover,
#tweet-quote:hover {
  opacity: 0.8;
}
footer {
  color: aliceblue;
  width: 100%;
  font-weight: 400;
  text-align: center;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) =========== */

@media screen and (max-width: 1024px) {
  .container-qb {
    display: block;
    padding: 5rem 0;
  }
  #title {
    font-size: 2.5rem;
    margin: 0 0.2rem 2.5rem 0.2rem;
  }
  #quote-box {
    min-height: 18rem;
    margin: 0 auto;
  }
  #author {
    margin: 2rem auto;
  }
}
/* =============== MEDIA QUERIES (SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
  #title {
    font-size: 1.6rem;
    margin: 1rem;
  }
  #quote-box {
    margin: auto 1rem;
    padding: 1rem;
    min-width: 15rem;
  }
  #author {
    margin: 1rem auto;
  }
  .buttons-quote {
    display: grid;
    grid-row: 2;
    grid-gap: 1rem;
    gap: 1rem;
    justify-items: end;
  }
}

