.projects-wrapper {
  background-color: antiquewhite;
  margin: 20px 0;
  width: 100%;
  gap: 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  background-color: blueviolet;
  border: 10px solid rgb(47, 40, 56);
}
